body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background-image: linear-gradient(to right bottom, #7d7dff94,#f3ff4f94,#ff3d3d94),url(./img/background.jpg);
  background-position: center;
  background-size: cover;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  
  position: relative;
}
.titles{
  background-color: #464646b2;
  padding: 20px;
  text-align: center;
}

.title-name{
  font-weight: 400;
  letter-spacing: 5px;
  color: #fff;
  font-size: 3em;
}

.form{
  padding: 15px;
  
  background: #ffffffb2;
  text-align: center;
  
}
.city,.country,.btn{
  margin: 5px;
  display: inline-block;
  height: 30px;
}

.city,.country{
  width: 350px;
}

.btn{
  height: 35px;
  background-color: #2fa3d1;
  border: 0;
  padding: 6px;
  width: 100px;
  color: #fff;
  
  cursor: pointer;
  transition: all .3s;
  
}

.btn:hover{
  transform: translateY(-3px);
}


.weather{
  margin: 10px;
  margin-left: 10%;
  margin-right:10%;
  ;
  color: rgb(255, 255, 255);
}

.error{
  text-align: center;
}

.location{
  font-size: 50px;
  display: inline-block;
  width: 60%;
 
}
.temperature{
  font-size: 60px;
  display: inline-block;
}
.humidity,.conditions{
  font-size: 30px;
}